section.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  display: none;

  &.opened {
    display: flex;
  }

  .close-button {
    position: absolute;
    top: 0;
    right: 0;
    background-color: unset;
    border: 0;
    cursor: pointer;

    width: 32px;
    height: 32px;
    padding: 8px;
    margin: 16px;

    .gt(@break-point-pad, {
      width: 80px;
      height: 80px;
      margin: 0;
    });

    img {
      width: 16px;
      height: 16px;
      .gt(@break-point-pad, {
        width: 50%;
        height: 50%;
      });
    }

    &:focus {
      outline: @border-width solid @main-color;
      outline-offset: -2 * @border-width;
    }
    &:hover {
      opacity: 0.8;
    }
  }
}

section.popup-image {
  background-color: fadeout(@bg-color, 20%);
  align-items: center;
  justify-content: center;

  .image {
    padding: 9px; // see #preview
    background-color: @border-color;

    max-width: 90%;
    max-height: 90%;
  }
}

section.popup-form {
  color: #333333;

  & > div {
    flex: 1 1 50%;
    background-color: @main-color;
    overflow-y: auto;
  }

  .picture {
    display: none;
    background-color: @bg-color;

    .gt(@break-point-desktop, {
      display: flex;
      align-items: center;
      justify-content: center;
    });

    img {
      max-width: 100%;
    }
  }

  form {
    padding: 56px @m-padding;

    max-width: 350px;
    margin: 0 auto;

    .gt(@break-point-pad, {
      max-width: 470px;
      box-sizing: content-box;
      padding-top: 70px;
      padding-bottom: 70px;
    });

    h1 {
      font-size: 32px;
      line-height: 41px;
      margin-bottom: 24px;

      .gt(@break-point-pad, {
        font-size: 54px;
        line-height: 120%;
      });
    }

    p {
      font-size: 14px;
      line-height: 24px;
      text-align: center;
      margin-bottom: 24px;

      .gt(@break-point-pad, {
        font-size: 20px;
        line-height: 150%;
        margin-bottom: 32px;
      });
    }

    .button {
      background-color: #407EC9;
      border-color: #407EC9;
      margin-top: 24px;

      .gt(@break-point-pad, {
        max-width: 470px;
        margin-top: 40px;
      });

      &:focus {
        outline: @border-width solid #333333;
        outline-offset: -@border-width;
      }
    }
  }
}
